import React from "react";
import Table from "react-bootstrap/Table";
import "./ErrorPopup.css";
import ReusableAlert from "../Alert/ReusableAlert";

const ErrorPopup = ({ errors }) => {
  console.log("Errors in ErrorPopup:", errors);

  // Group errors by row number
  const groupedErrors = errors.reduce((acc, error) => {
    const rowMatch = error.match(/Row (\d+):/);
    const rowNum = rowMatch ? rowMatch[1] : "-";
    const columnMatch = error.match(/"([^"]*)" is required/);
    const columnName = columnMatch ? columnMatch[1] : "-";
    const errorMsg = rowMatch ? error.split(": ")[1] : error;

    if (!acc[rowNum]) {
      acc[rowNum] = {
        errorMessages: [],
        columnNames: [],
      };
    }

    acc[rowNum].errorMessages.push(errorMsg);
    acc[rowNum].columnNames.push(columnName);
    return acc;
  }, {});

  const errorMessage = (
    <>
      <strong>Note:</strong> Below reports show only uploaded spreadsheet error
      rows. To resolve below issues, do the required changes in the spreadsheet
      and re-import.
    </>
  );

  return (
    <>
      <ReusableAlert
        variant="danger"
        message={errorMessage}
        className="text-left-align text-sm-start mb-3"
      />
      <div className="table-responsive">
        <Table striped bordered hover>
          <thead className="table-light">
            <tr>
              <th>Row Number</th>
              <th>Error Description</th>
              <th>Column Name</th>
            </tr>
          </thead>
          <tbody>
            {Object.entries(groupedErrors).length > 0 ? (
              Object.entries(groupedErrors).map(
                ([rowNum, { errorMessages, columnNames }]) => (
                  <tr key={rowNum}>
                    <td>{rowNum}</td>
                    <td className="error-messages">
                      {errorMessages.join(" • ")}
                    </td>
                    <td className="column-names">{columnNames.join(" • ")}</td>
                  </tr>
                )
              )
            ) : (
              <tr>
                <td colSpan={3} className="text-center">
                  No Errors Found
                </td>
              </tr>
            )}
          </tbody>
        </Table>
      </div>
    </>
  );
};

export default ErrorPopup;
